header {
  background: #1E1E1E;
  height: 80px;
  line-height: 80px;
}
header .navbar {
  width: 100%;
  height: 100%;
}
header .navbar ul {
  height: 100%;
  overflow: hidden;
}
header .navbar li {
  height: 100%;
  padding: 0px 10px;
  float: left;
}
header .navbar li a {
  position: relative;
  left: 0px;
  top: 0px;
  display: block;
  color: #A0A0A0;
  font-size: 1.5rem;
}
header .navbar li a:after {
  position: absolute;
  left: 0px;
  bottom: 20px;
  content: "1212";
  height: 2px;
  width: 100%;
  background: #fff;
  display: none;
}
header .navbar li a.active,
header .navbar li a:hover {
  color: #fff;
}
header .navbar li a.active:after,
header .navbar li a:hover:after {
  display: block;
}



