.objectWrapper .text {
  width: 100%;
  height: 40px;
  outline: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  display: block;
  padding: 0px;
  border: 0px;
  border-bottom: 1px solid gray;
  border-box: border-sizing;
  text-indent: 15px;
  font-size: 15px;
}
.objectWrapper .tree {
  width: 100%;
  height: calc(100% - 40px);
  overflow: hidden;
  overflow-y: auto;
}
.objectWrapper .tree .objectNode {
  width: 100%;
  height: auto;
  line-height: 35px;
  border-bottom: 1px solid #D5D5D5;
}
.objectWrapper .tree .objectNode.open > .des > .ope {
  background-image: url(../../../../../img/icon/up.png);
}
.objectWrapper .tree .objectNode.open > .children {
  display: block;
}
.objectWrapper .tree .objectNode.isObj .des {
  background: #F2F2F2;
}
.objectWrapper .tree .objectNode .des * {
  vertical-align: middle;
}
.objectWrapper .tree .objectNode .des .key {
  color: #C790CB;
}
.objectWrapper .tree .objectNode .des .value {
  color: #4A4429;
}
.objectWrapper .tree .objectNode .des .type {
  color: #CF4845;
}
.objectWrapper .tree .objectNode .ope {
  width: 35px;
  height: 35px;
  display: inline-block;
  background: url(../../../../../img/icon/down.png) no-repeat left center;
  background-size: 20px 20px;
}
.objectWrapper .tree .objectNode .children {
  width: calc(100% - 40px);
  margin-left: 40px;
  display: none;
}



